.kor2an-home,.work-home,.publicwork-home,.specialwork-home{
    display: none;
}
.home-page{
    margin-top: 90px;
    h1{
        width: 20%;
        @media (max-width: 767px) {
            display: none;
        }
    }
    .page-home{
        width: 50%;
        position: relative;
        top: 50px;
        @media (max-width: 767px) {
            width: 100%;
            margin-top: 60px;
        }
        .link{
            &:nth-child(4){
                margin-bottom: 20px;
            }
            @media (max-width: 767px) {
                width: 300px;
            }
            @media (min-width: 768px) and (max-width: 992px) {
                width: 250px;
            }
        }
        .hawl-tatbik{
            width: 100px;
            background: #56dc56;
            margin: auto;
            padding: 10px;
            position: relative;
            border-radius: 7px;
            cursor: pointer;
            .i1{
                font-style: italic;
                font-size: 20px;
                color: #fff;
                text-align: center;
                width: 26px;
                margin: auto;
                background: #01b6f2;
                position: relative;
                z-index: 2;
                &::before{
                    content: "";
                    display: block;
                    width: 10px;
                    padding: 3px;
                    position: absolute;
                    top: 16px;
                    left: 3px;
                    background: #01b6f2;
                    z-index: -1;
                    transform: skew(10deg, -35deg);
                }
            }
            .i2{
                font-style: italic;
                font-size: 20px;
                color: #fff;
                text-align: center;
                width: 26px;
                margin: auto;
                background: #ff0;
                position: absolute;
                top: 6%;
                left: 47%;
                z-index: 1;
                &::before{
                    content: "";
                    display: block;
                    width: 10px;
                    padding: 3px;
                    position: absolute;
                    top: 15px;
                    right: 2px;
                    background: yellow;
                    z-index: -1;
                    transform: skew(-10deg, 35deg);
                }
            }
            p{
                font-size: 20px;
                text-align: center;
                color: #555;
                margin-bottom: 0;
            }
        }
    }
    img{
        width: 30%;
        @media (max-width: 767px) {
            position: absolute;
            top: 0;
            width: 165px;
        }
    }
}

.kor2an-home{
    margin-top: 150px;
    h1{
        @media (max-width: 767px) {
            display: none;
        }
    }
    .kor2an-page{
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        flex-direction: row;
        .link{
            @media (max-width: 767px) {
                width: 300px;
            }
        }
        img{
            border-radius: 7px;
            border: 3px solid #aaa;
            padding: 2px;
            width: 40%;
            height: 170px;
            @media (max-width: 767px) {
                position: absolute;
                top: 0;
                width: 165px;
                height: 130px;
            }
        }
        @media (max-width: 767px) {
            width: 100%;
            margin-top: 60px;
        }
    }
}
.work-home{
    margin-top: 150px;
    h1{
        @media (max-width: 767px) {
            display: none;
        }
    }
    .work-page{
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        flex-direction: row;
        .work{
            .link{
                @media (max-width: 767px) {
                    width: 300px;
                }
            }
            @media (max-width: 767px) {
                margin-top: 40px;
            }
        }
        img{
            border-radius: 7px;
            border: 3px solid #aaa;
            padding: 2px;
            width: 40%;
            height: 170px;
            @media (max-width: 767px) {
                position: absolute;
                top: 0;
            }
        }
        @media (max-width: 767px) {
            flex-direction: column;
        }
    }
}
.publicwork-home{
    .publicwork-page{
        .publicwork{
            display: grid;
            grid-template-columns: repeat(3,calc(100% / 3));
            @media (max-width: 767px) {
                display: block;
            }
            @media (min-width: 768px) and (max-width: 992px) {
                grid-template-columns: repeat(2,50%);
            }
            .public{
                &:last-of-type{
                    .link2{
                        @media (min-width: 768px) and (max-width: 992px) {
                            position: relative;
                            left: -100%;
                            top: -14%;
                        }
                    }
                    .link3{
                        @media (min-width: 768px) and (max-width: 992px) {
                            position: relative;
                            top: -14%;
                        }
                    }
                    .link4{
                        @media (min-width: 768px) and (max-width: 992px) {
                            position: relative;
                            left: -100%;
                            top: -28%;
                        }
                    }
                    .link5{
                        @media (min-width: 768px) and (max-width: 992px) {
                            position: relative;
                            top: -28%;
                        }
                    }
                    .link6{
                        @media (min-width: 768px) and (max-width: 992px) {
                            position: relative;
                            left: -100%;
                            top: -42%;
                        }
                    }
                    .link7{
                        @media (min-width: 768px) and (max-width: 992px) {
                            position: relative;
                            left: -50%;
                            top: -42%;
                        }
                    }
                }
                .link{
                    width: 300px;
                }
            }
        }
    }
}


.specialwork-home{
    .specialwork-page{
        .specialwork{
            display: grid;
            grid-template-columns: repeat(2,50%);
            @media (max-width: 767px) {
                display: block;
            }
            @media (min-width:768px) and (max-width: 992px) {
                grid-template-columns: repeat(1,100%);
            }
            .special{
                .link{
                    width: 400px;
                    @media (min-width:768px) and (max-width: 992px) {
                        width: 100%;
                    }
                    @media (max-width:767px) {
                        width: 300px;
                    }
                }
            }
        }
    }
}
