.wa7ed-w3eshrin{
    display: none;
    text-align: justify;
    font-size: 25px;
    margin: 10px;
    p{
        &:nth-of-type(1){
            margin-bottom: 0;
        }
        &:nth-of-type(2){
            color: red;
            border-bottom: 1px solid blue;
            padding-bottom: 10px;
            width: fit-content;
            margin-top: 0;
        }
        &:nth-of-type(3){
            margin-bottom: 0;
        }
        &:last-of-type{
            color: red;
            margin-top: 0;
        }
    }
    @media (max-width: 767px) {
        font-size: 20px;
    }
}