.about{
    display: none;
    color: #777;
    .header{
        text-align: center;
        img{
            width: 200px;
        }
        h1{
            margin-top: 0;
            font-weight: 500;
        }
    }
    .version{
        width: 260px;
        margin: auto;
        h3{
            text-align: center;
        }
        p{
            text-align: justify;
            border-bottom: 1px solid #000;
            padding-bottom: 15px;
            font-size: 17px;
        }
    }
    .share{
        margin: auto;
        width: 260px;
        .rate{
            img{
                border-radius: 50%;
                background: red;
                padding: 5px;
                display: inline-block;
            }
            a{
                position: relative;
                text-decoration: none;
                color: #777;
                margin-right: 20px;
                font-size: 18px;
                top: -18px;
            }
        }
        .more{
            img{
                border-radius: 50%;
                background: #01b6f2;
                padding: 5px;
                display: inline-block;
            }
            a{
                position: relative;
                text-decoration: none;
                color: #777;
                margin-right: 20px;
                font-size: 18px;
                top: -18px;
            }
        }
        .shared{
            img{
                border-radius: 50%;
                display: inline-block;
                padding: 5px;
                background: yellow;
            }
            a{
                position: relative;
                text-decoration: none;
                color: #777;
                margin-right: 20px;
                font-size: 18px;
                top: -18px;
            }
        }
    }
    .footer{
        background-color: #0b0bbd;
        text-align: center;
        margin-top: 20px;
        padding: 40px;
        span{
            font-size: 20px;
            color: white;
            font-style: oblique;
            position: relative;
            top: -10px;
        }
        .whats{
            background: green;
            border-radius: 50%;
            padding: 2px;
            margin-right: 10px;
        }
        .fb{
            background: #16166e;
            border-radius: 50%;
            padding: 2px;
            margin-right: 10px;
        }
    }
}